import React from 'react';

class Button extends React.Component{
    render(){
        return(
            <button onClick={this.props.onClick}>{this.props.name}</button>
        );
    }
};

class TextBox extends React.Component{
    render(){
        return(
            <input type='text' value={this.props.value} onChange={this.props.onChange}/>
        );
    }
};

class App extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            list: [],
            newEntry: "",
            blank: 0,
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.saveFile = this.saveFile.bind(this);
    }

    handleChange(event){
        this.setState({newEntry: event.target.value});
    }

    checkMatch(item){
        for(var i=0; i<this.state.list.length; i++){
            if(this.state.list[i] === item){
                return false;
            }
        }
        return true;
    }

    handleClick(){
        if(this.state.newEntry !== "" && this.checkMatch(this.state.newEntry)){
            this.setState({list: this.state.list.concat(this.state.newEntry), newEntry: ""});
        }
    }

    del(a){
        var list = [];
        for(var i = 0; i < this.state.list.length; i++){
            if(this.state.list[i] !== a)
                list.push(this.state.list[i]);
        }
        this.setState({list: list});
    }

    saveFile(){
        var strg = "";
        
        for(var i = 0; i < this.state.list.length; i++){
            strg = strg + "~" + this.state.list[i];
        }

        var xhttp = new XMLHttpRequest();
        xhttp.onreadystatechange = function () {
            if (this.readyState === 4 && this.status === 200) {
                document.getElementById("savestat").innerHTML =
                    this.responseText;
            }
        };
        xhttp.open("POST", "https://desolate-castle-50423.herokuapp.com/?data=" + strg, true);
        xhttp.send();
    }

    render(){
        const listItems = this.state.list.map((a) => <li key={a}><Button name="Delete" onClick={this.del.bind(this, a)}/> {a}</li>);
        return(
            <div>
                <TextBox value={this.state.newEntry} onChange={this.handleChange}/>
                <Button name="Add" onClick={this.handleClick}/>
                <Button name="Save" onClick={this.saveFile}/>
                <ul>{listItems}</ul>
				<p id="savestat"></p>
            </div>
        );
    }
};

export default App;